

import { FocusMonitor } from '@angular/cdk/a11y';
import { Direction, Directionality } from '@angular/cdk/bidi';
import { BooleanInput } from '@angular/cdk/coercion';
import { DOWN_ARROW, ENTER, UP_ARROW } from '@angular/cdk/keycodes';
import {
  AfterViewInit,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ElementRef,
  EventEmitter,
  forwardRef,
  Host,
  Inject,
  Input,
  NgZone,
  OnChanges,
  OnDestroy,
  OnInit,
  Optional,
  Output,
  Self,
  SimpleChanges,
  ViewChild,
  ViewEncapsulation
} from '@angular/core';
import { ControlValueAccessor, FormControl, FormGroupDirective, NgControl, NgForm } from '@angular/forms';
import { fromEvent, Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { ButtonSize } from "tds-ui/core/config";
import { TDSFormField, TDSFormFieldControl, TDS_FORM_FIELD } from 'tds-ui/form-field';
import { InputBoolean, OnChangeType, OnTouchedType, TDSHelperObject, TDSSafeAny } from 'tds-ui/shared/utility';
import { isNotNil } from 'tds-ui/core/util';
import { coerceBooleanProperty } from 'tds-ui/shared/coercion';
import { TDSIsErrorState } from 'tds-ui/shared/common';

@Component({
  selector: 'tds-input-number',
  exportAs: 'tdsInputNumber',
  templateUrl: './input-number.component.html',
  providers: [
    {
      provide: TDSFormFieldControl,
      useExisting: forwardRef(() => TDSInputNumberComponent),
      multi: true
    }
  ],
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  host: {
    '[class.tds-input-number-focused]': 'isFocused',
    '[class.tds-input-number-disabled]': 'disabled',
    '[class.bg-neutral-3-50]': 'disabled',
    '[class.cursor-not-allowed]': 'disabled',
    '[class.tds-input-number-rtl]': `dir === 'rtl'`,
    '[class.border]': "!parentIsFormField",
    '[class.border-neutral-2-200]': "!parentIsFormField && !isFocused",
    '[class.dark:border-d-neutral-2-200]': "!parentIsFormField && !isFocused",
    '[class.border-primary-1]': "!parentIsFormField && isFocused",
    '[class.ring]': "!parentIsFormField && isFocused",
    '[class.ring-opacity-20]': "!parentIsFormField && isFocused",
    '[class.ring-primary-1]': "!parentIsFormField && isFocused",
    '[class.rounded]': "!parentIsFormField ",
    '[class.bg-white]': "!parentIsFormField && !disabled",
    '[class.dark:bg-d-neutral-3-50]': "!parentIsFormField && !disabled",
    'class': 'inline-block box-border relative min-w-0 group outline-none w-full'
  }
})
export class TDSInputNumberComponent implements ControlValueAccessor, TDSFormFieldControl<any>, AfterViewInit, OnChanges, OnInit, OnDestroy {
  static ngAcceptInputType_disabled: BooleanInput;
  static ngAcceptInputType_autoFocus: BooleanInput;
  private autoStepTimer?: TDSSafeAny;
  private parsedValue?: string | number;
  public value: number | null = null;
  private destroy$ = new Subject<void>();
  stateChanges = new Subject<void>();
  parentIsFormField: boolean = false;
  displayValue?: string | number;
  isFocused = false;
  disabledUp = false;
  disabledDown = false;
  dir: Direction = 'ltr';
  onChange: OnChangeType = () => { };
  onTouched: OnTouchedType = () => { };
  @Output() readonly tdsBlur = new EventEmitter();
  @Output() readonly tdsFocus = new EventEmitter();
  @ViewChild('inputElement', { static: true }) inputElement!: ElementRef<HTMLInputElement>;
  @Input() min: number = -Infinity;
  @Input() max: number = Infinity;
  @Input() parser = (value: string) =>
    value
      .trim()
      .replace(/ã€‚/g, '.')
      .replace(/[^\w\.-]+/g, '');
  @Input() precision?: number;
  @Input() precisionMode: 'cut' | 'toFixed' | ((value: number | string, precision?: number) => number) = 'toFixed';
  @Input() placeholder = '';
  @Input() step = 1;
  @Input() inputMode: string = 'decimal';
  @Input() id: string | null = null;
  @Input() @InputBoolean() disabled = false;
  @Input() @InputBoolean() autoFocus = false;
  @Input() formatter: (value: number) => string | number = value => value;
  private _size: ButtonSize = 'md'
  @Input()
  get size() {
    return this._size;

  }
  set size(value: ButtonSize) {
    if (this._size != value) {
      this._size = value;
      this.stateChanges.next();
    }
  }
  // ngControl, focused, empty, required,
  errorState: boolean = false;
  updateErrorState() {
      const oldState = this.errorState;
      const parent = this._parentFormGroup || this._parentForm;
      // const matcher = this.errorStateMatcher || this._defaultErrorStateMatcher;
      const control = this.ngControl ? this.ngControl.control as FormControl : null;
      const newState = this._isErrorState(control, parent);
      if (newState !== oldState) {
          this.errorState = newState;
          this.stateChanges.next();
          this.cdr.markForCheck();
      }
  }
  private _isErrorState(control: FormControl | null, form: FormGroupDirective | NgForm | null): boolean {
    return  TDSIsErrorState(control,form);
  }
  get focused() {
    return this.isFocused
  }
  get empty() {
    return !TDSHelperObject.hasValue(this.value);
  }
  @Input()
  get required(): boolean {
    return this._required;
  }
  set required(value: boolean) {
    this._required = coerceBooleanProperty(value);
    this.stateChanges.next();
  }
  private _required = false;
  @Input() @InputBoolean() hiddenIcon = false;
  /**
   * 
   */
  @Input() @InputBoolean() autoFormat = false;
  // setDescribedByIds, onContainerClickts
  setDescribedByIds(ids: string[]) {
    if (ids.length) {
      this.elementRef.nativeElement.setAttribute('aria-describedby', ids.join(' '));
    } else {
      this.elementRef.nativeElement.removeAttribute('aria-describedby');
    }
  }
  onContainerClick(e: MouseEvent) {
    // e.preventDefault();
    // e.stopPropagation();

  }
  onModelChange(value: string): void {
    const parsedValue = this.parser(value);
    this.parsedValue = parsedValue;
    const validValue = this.getCurrentValidValue(this.parsedValue);
    this.setValue(validValue);
    if(this.autoFormat)
    {
      this.updateDisplayValue(this.value!);
    }
  }

  getCurrentValidValue(value: string | number): number {
    let val = value;
    if (val === '') {
      val = '';
    } else if (!this.isNotCompleteNumber(val)) {
      val = `${this.getValidValue(val)}`;
    } else {
      val = this.value!;
    }
    return this.toNumber(val);
  }

  // '1.' '1x' 'xx' '' => are not complete numbers
  isNotCompleteNumber(num: string | number): boolean {
    return (
      isNaN(num as number) ||
      num === '' ||
      num === null ||
      !!(num && num.toString().indexOf('.') === num.toString().length - 1)
    );
  }

  getValidValue(value?: string | number): string | number | undefined {
    let val = parseFloat(value as string);
    // https://github.com/tds-design/tds-design/issues/7358
    if (isNaN(val)) {
      return value;
    }
    if (val < this.min) {
      val = this.min;
    }
    if (val > this.max) {
      val = this.max;
    }
    return val;
  }

  toNumber(num: string | number): number {
    if (this.isNotCompleteNumber(num)) {
      return num as number;
    }
    const numStr = String(num);
    if (numStr.indexOf('.') >= 0 && isNotNil(this.precision)) {
      if (typeof this.precisionMode === 'function') {
        return this.precisionMode(num, this.precision);
      } else if (this.precisionMode === 'cut') {
        const numSplit = numStr.split('.');
        numSplit[1] = numSplit[1].slice(0, this.precision);
        return Number(numSplit.join('.'));
      }
      return Number(Number(num).toFixed(this.precision));
    }
    return Number(num);
  }

  getRatio(e: KeyboardEvent): number {
    let ratio = 1;
    if (e.metaKey || e.ctrlKey) {
      ratio = 0.1;
    } else if (e.shiftKey) {
      ratio = 10;
    }
    return ratio;
  }

  down(e: MouseEvent | KeyboardEvent, ratio?: number): void {
    if (!this.isFocused) {
      this.focus();
    }
    this.onStep('down', e, ratio);
  }

  up(e: MouseEvent | KeyboardEvent, ratio?: number): void {
    if (!this.isFocused) {
      this.focus();
    }
    this.onStep('up', e, ratio);
  }

  getPrecision(value: number): number {
    const valueString = value.toString();
    if (valueString.indexOf('e-') >= 0) {
      return parseInt(valueString.slice(valueString.indexOf('e-') + 2), 10);
    }
    let precision = 0;
    if (valueString.indexOf('.') >= 0) {
      precision = valueString.length - valueString.indexOf('.') - 1;
    }
    return precision;
  }

  // step={1.0} value={1.51}
  // press +
  // then value should be 2.51, rather than 2.5
  // if this.props.precision is undefined
  // https://github.com/react-component/input-number/issues/39
  getMaxPrecision(currentValue: string | number, ratio: number): number {
    if (isNotNil(this.precision)) {
      return this.precision;
    }
    const ratioPrecision = this.getPrecision(ratio);
    const stepPrecision = this.getPrecision(this.step);
    const currentValuePrecision = this.getPrecision(currentValue as number);
    if (!currentValue) {
      return ratioPrecision + stepPrecision;
    }
    return Math.max(currentValuePrecision, ratioPrecision + stepPrecision);
  }

  getPrecisionFactor(currentValue: string | number, ratio: number): number {
    const precision = this.getMaxPrecision(currentValue, ratio);
    return Math.pow(10, precision);
  }

  upStep(val: string | number, rat: number): number {
    const precisionFactor = this.getPrecisionFactor(val, rat);
    const precision = Math.abs(this.getMaxPrecision(val, rat));
    let result;
    if (typeof val === 'number') {
      result = ((precisionFactor * val + precisionFactor * this.step * rat) / precisionFactor).toFixed(precision);
    } else {
      result = this.min === -Infinity ? this.step : this.min;
    }
    return this.toNumber(result);
  }

  downStep(val: string | number, rat: number): number {
    const precisionFactor = this.getPrecisionFactor(val, rat);
    const precision = Math.abs(this.getMaxPrecision(val, rat));
    let result;
    if (typeof val === 'number') {
      result = ((precisionFactor * val - precisionFactor * this.step * rat) / precisionFactor).toFixed(precision);
    } else {
      result = this.min === -Infinity ? -this.step : this.min;
    }
    return this.toNumber(result);
  }

  onStep<T extends keyof TDSInputNumberComponent>(type: T, e: MouseEvent | KeyboardEvent, ratio: number = 1): void {
    this.stop();
    e.preventDefault();
    if (this.disabled) {
      return;
    }
    const value = this.getCurrentValidValue(this.parsedValue!) || 0;
    let val = 0;
    if (type === 'up') {
      val = this.upStep(value, ratio);
    } else if (type === 'down') {
      val = this.downStep(value, ratio);
    }
    const outOfRange = val > this.max || val < this.min;
    if (val > this.max) {
      val = this.max;
    } else if (val < this.min) {
      val = this.min;
    }
    this.setValue(val);
    this.updateDisplayValue(val);
    this.isFocused = true;
    this.stateChanges.next();
    if (outOfRange) {
      return;
    }
    this.autoStepTimer = setTimeout(() => {
      (this[type] as (e: MouseEvent | KeyboardEvent, ratio: number) => void)(e, ratio);
    }, 300);
  }

  stop(): void {
    if (this.autoStepTimer) {
      clearTimeout(this.autoStepTimer);
    }
  }

  setValue(value: number): void {
    if (`${this.value}` !== `${value}`) {
      this.onChange(value);
    }
    this.value = value;
    this.parsedValue = value;
    this.disabledUp = this.disabledDown = false;
    if (value || value === 0) {
      const val = Number(value);
      if (val >= this.max) {
        this.disabledUp = true;
      }
      if (val <= this.min) {
        this.disabledDown = true;
      }
    }
  }

  updateDisplayValue(value: number): void {
    const displayValue = isNotNil(this.formatter(value)) ? this.formatter(value) : '';
    this.displayValue = displayValue;
    this.inputElement.nativeElement.value = `${displayValue}`;
  }

  // onKeyDown(e: KeyboardEvent): void {
  //   if (e.keyCode === UP_ARROW) {
  //     const ratio = this.getRatio(e);
  //     this.up(e, ratio);
  //     this.stop();
  //   } else if (e.keyCode === DOWN_ARROW) {
  //     const ratio = this.getRatio(e);
  //     this.down(e, ratio);
  //     this.stop();
  //   } else if (e.keyCode === ENTER) {
  //     this.updateDisplayValue(this.value!);
  //   }
  // }

  writeValue(value: number): void {
    this.value = value;
    this.setValue(value);
    this.updateDisplayValue(value);
    this.cdr.markForCheck();
  }

  registerOnChange(fn: OnChangeType): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: OnTouchedType): void {
    this.onTouched = fn;
  }

  setDisabledState(disabled: boolean): void {
    this.disabled = disabled;
    this.cdr.markForCheck();
  }

  focus(): void {
    this.focusMonitor.focusVia(this.inputElement, 'keyboard');
  }

  blur(): void {
    this.inputElement.nativeElement.blur();
  }

  constructor(
    private ngZone: NgZone,
    private elementRef: ElementRef,
    private cdr: ChangeDetectorRef,
    private focusMonitor: FocusMonitor,
    @Optional() private directionality: Directionality,
    @Optional() @Inject(TDS_FORM_FIELD) @Host() public _formField: TDSFormField,
    @Optional() public _parentForm: NgForm,
    @Optional() public _parentFormGroup: FormGroupDirective,
    @Optional() @Self() public ngControl: NgControl,
  ) {
    // TODO: move to host after View Engine deprecation
    this.elementRef.nativeElement.classList.add('tds-input-number');
    if (this.ngControl != null) {
      this.ngControl.valueAccessor = this;
    }
    if (this._formField) {
      // this.size = this._formField.size;
      this.parentIsFormField = true;
    }
  }

  ngOnInit(): void {
    this.focusMonitor
      .monitor(this.elementRef, true)
      .pipe(takeUntil(this.destroy$))
      .subscribe(focusOrigin => {
        if (!focusOrigin) {
          this.isFocused = false;
          this.updateDisplayValue(this.value!);
          this.tdsBlur.emit();
          this.stateChanges.next();
          Promise.resolve().then(() => this.onTouched());
        } else {
          this.isFocused = true;
          this.stateChanges.next();
          this.tdsFocus.emit();
        }
      });

    this.dir = this.directionality.value;
    this.directionality.change?.pipe(takeUntil(this.destroy$)).subscribe((direction: Direction) => {
      this.dir = direction;
    });
    this.ngZone.runOutsideAngular(() => {
      fromEvent(this.inputElement.nativeElement, 'keyup')
        .pipe(takeUntil(this.destroy$))
        .subscribe(() => this.stop());

      fromEvent<KeyboardEvent>(this.inputElement.nativeElement, 'keydown')
        .pipe(takeUntil(this.destroy$))
        .subscribe(event => {
          const { keyCode } = event;
          if (keyCode !== UP_ARROW && keyCode !== DOWN_ARROW && keyCode !== ENTER) {
            return;
          }

          this.ngZone.run(() => {
            if (keyCode === UP_ARROW) {
              const ratio = this.getRatio(event);
              this.up(event, ratio);
              this.stop();
            } else if (keyCode === DOWN_ARROW) {
              const ratio = this.getRatio(event);
              this.down(event, ratio);
              this.stop();
            } else {
              this.updateDisplayValue(this.value!);
            }

            this.cdr.markForCheck();
          });
        });
    });

  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.formatter && !changes.formatter.isFirstChange()) {
      const validValue = this.getCurrentValidValue(this.parsedValue!);
      this.setValue(validValue);
      this.updateDisplayValue(validValue);
    }

  }

  ngAfterViewInit(): void {
    if (this.autoFocus) {
      this.focus();
    }
  }

  ngOnDestroy(): void {
    this.focusMonitor.stopMonitoring(this.elementRef);
    this.destroy$.next();
    this.destroy$.complete();
  }
  ngDoCheck() {

    if (this.ngControl) {
        this.updateErrorState();
    }
}
}
